import { createApp } from 'vue'
import { ActionBar, ActionBarIcon, ActionBarButton,Popover, Divider, Popup, Overlay, Loading, Dialog,
  Uploader,ContactCard, Form, AddressEdit, AddressList, Field, CellGroup, Cell, SwipeCell, Icon, Stepper, Card,
  Checkbox, CheckboxGroup, Button, Swipe,Badge, SwipeItem, PullRefresh, List, Tab, Tabs, SubmitBar, Toast, Skeleton } from 'vant'
import App from './App.vue'
import store from './store'
import router from './router'
import 'lib-flexible/flexible'
import 'vant/lib/index.css'; // 全局引入样式
import moment from 'moment';
const app = createApp(App) // 创建实例
import './icons'

import jwx from '@/utils/jweixin.js'
app.config.globalProperties.$jwx = jwx
// 全局过滤器
app.config.globalProperties.$filters = {
  prefix(url) {
    if (url && url.startsWith('http')) {
      return url
    } else {
      url = `http://backend-api-01.newbee.ltd${url}`
      return url
    }
  }
}
app.config.globalProperties.$moment=moment;
import { registerSvgIconComp } from '@/icons/index'
registerSvgIconComp(app)

app.use(ActionBarButton)
  .use(ActionBarIcon)
  .use(ActionBar)
    .use(Popover)
    .use(Uploader)
    .use(Badge)
  .use(Divider)
  .use(Popup)
  .use(Overlay)
  .use(Loading)
  .use(Dialog)
  .use(Toast)
  .use(ContactCard)
  .use(Form)
  .use(AddressEdit)
  .use(AddressList)
  .use(Field)
  .use(CellGroup)
  .use(Cell)
  .use(SwipeCell)
  .use(Icon)
  .use(Stepper)
  .use(Card)
  .use(Button)
  .use(Swipe)
  .use(SwipeItem)
  .use(PullRefresh)
  .use(List)
  .use(Tab)
  .use(Tabs)
  .use(SubmitBar)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Skeleton)

app.use(router)
app.use(store)

app.mount('#app')