import jweixin from 'weixin-js-sdk'
import axios from '../utils/axios'

export default {
    //判断是否在微信中
    isWechat: function() {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/micromessenger/i) == 'micromessenger') {
            //console.log('是微信客户端')
            return true;
        } else {
            //console.log('不是微信客户端')
            return false;
        }
    },
    // 初始化
    initJssdk: function(callback) {
        // 记录进入app时的url
        if (typeof window.entryUrl === 'undefined' || window.entryUrl === '') {
            window.entryUrl = location.href.split('#')[0]
        }
        let isiOS = !!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        let signLink = isiOS ? window.entryUrl : location.href.split('#')[0];
        var uri = encodeURIComponent(signLink);
        axios.get('wx_api/token/getWxConfig', {params:{ 'url':uri }}).then((res)=>{
            jweixin.config({
                debug: false,
                appId: res.data.data.appId,
                timestamp: res.data.data.timestamp,
                nonceStr: res.data.data.nonceStr,
                signature: res.data.data.signature,
                jsApiList: ['updateAppMessageShareData', 'updateTimelineShareData', 'editAddress', 'chooseImage',
                    'onMenuShareAppMessage', 'onMenuShareTimeline', 'chooseImage', 'previewImage', 'uploadImage',
                    'downloadImage', 'chooseWXPay'
                ]
            });
            jweixin.ready(function() {
                console.log('config注入成功')
                if (callback) {
                    callback();
                }
            })
        });
    },
    //在需要定位页面调用
    getlocation: function(callback) {
        if (!this.isWechat()) {
            //console.log('不是微信客户端')
            return;
        }
        this.initJssdk(function(res) {
            console.log(res)
            jweixin.ready(function() {
                jweixin.getLocation({
                    type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                    success: function(res) {
                        // console.log(res);
                        callback(res)
                    },
                    fail: function(res) {
                        console.log(res)
                    },
                    // complete:function(res){
                    //     console.log(res)
                    // }
                });
            });
        });
    },
    openlocation: function(data) { //打开位置
        if (!this.isWechat()) {
            //console.log('不是微信客户端')
            return;
        }
        this.initJssdk(function(res) {
            console.log(res)
            jweixin.ready(function() {
                jweixin.openLocation({ //根据传入的坐标打开地图
                    latitude: data.latitude,
                    longitude: data.longitude
                });
            });
        });
    },
    chooseImage: function(callback) { //选择图片
        if (!this.isWechat()) {
            //console.log('不是微信客户端')
            return;
        }
        //console.log(data);
        this.initJssdk(function(res) {
            console.log(res)
            jweixin.ready(function() {
                jweixin.chooseImage({
                    count: 1,
                    sizeType: ['compressed'],
                    sourceType: ['album'],
                    success: function(res) {
                        callback(res)
                    }
                })
            });
        });
    },
    //微信支付
    wxpay: function(params = {}) {
        if (!this.isWechat()) {
            //console.log('不是微信客户端')
            return;
        }
        this.initJssdk(function() {
            // console.log(params)
            jweixin.chooseWXPay({
                timestamp: params.data.timeStamp,
                nonceStr: params.data.nonceStr,
                package: params.data.package,
                signType: params.data.signType,
                paySign: params.data.paySign,
                success: function(res) {
                    // console.log(res);
                    // console.log('微信JSAPI返回支付成功')
                    params.success(res)
                },
                cancel: function(res) {
                    params.fail(res)
                    // console.log('微信JSAPI返回支付失败')
                },
                // complete:function(res){
                //     console.log(res)
                // }
            });

        });
    },
    share: function(data) {
        this.initJssdk(function() {
            var shareData = {
                title: data && data.title ? data.title : 'aaa',
                desc: data && data.desc ? data.desc : '零钱提现',
                link: data && data.link ? data.link : window.location.href,
                imgUrl: data && data.imgUrl ? data.imgUrl : 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-weitao/d724aa70-eac6-11ea-b680-7980c8a877b8.jpg',
                success: function(res) {
                    console.log('设置成功'+res)
                },
                cancel: function(res) { console.log(res)}
            };
            debugger;
            //分享给朋友接口
            jweixin.onMenuShareAppMessage(shareData);
            //分享到朋友圈接口
            jweixin.onMenuShareTimeline(shareData);
        });
    },
    // 选择通讯地址
    chooseAddress: function(callback) {
        this.initJssdk(function() {
            jweixin.openAddress({
                success: function(res) {
                    callback(res)
                }
            })
        })
    }
}
